.main-dashboard {
    margin-bottom: 16px;
    border-bottom: 1px solid #e6e9ef;
    font-family: "roboto-regular", sans-serif;
    font-size: 15px;
    width: 100%;
    justify-content: space-between;

    .Whatsapp-share{
        justify-self: flex-end;
        margin-right: 5%;

    }

    span {
        font-family: roboto-regular, helvetica, sans-serif;
        color: black;
    }
    .main-dashboard-controllers{
        width: 100%;
        justify-content: space-between;
        align-items: center;

    }

    .main-table-container {
        gap: 10px;

        cursor: pointer;
        max-width: 180px;
        min-width: 130px;
        padding: 8px 12px 8px 12px;
        &:hover {
            background-color: #e6e9ef;
            border-radius: 4px;
        }

        

        // span{
        //     // width: 100%;
        //     // height: 100%;
        //     overflow-x: hidden;
        //     overflow-y: hidden;
        //     text-overflow: ellipsis;
        //     white-space: nowrap;
        //     border: 1px solid transparent;
        //     // padding: 0 2px;
        // }
    }

    .dashboard-btn{
        width: 100px;
        padding: 8px 12px 8px 12px;
        cursor: pointer;


        &:hover {
            border-radius: 4px;
            background-color: #e6e9ef;
        }

        section.board-header {
            width: 100%;
            height: 70px;
            margin: 0;
            margin-bottom: 130px;
            position: sticky;
            margin-left: 20px;
            left: 0;
            z-index: 200;
        }

    }
}

@media (min-width: 640px) {

    .main-dashboard {
    .Whatsapp-share{
        margin-right: 2%;

    }
}
  }