.task-preview {
  display: flex;
  position: relative;
  width: 100%;
  height: 34px;
  line-height: 34px;
  margin-bottom: 2px;
  font-size: 0.8125rem;
  text-align: center;
  margin-left: 11px;

  .task-title-cell {
    border-bottom: 1px solid #e6e9ef;
    background-color: #f5f6f8ed;
    box-shadow: 0 3px 4px -5px #484444;
    width: 180px;
    text-align: left;
    position: sticky;
    left: 0;
    z-index: 3;
    .title-edit-container {
      position: sticky;
      left: 0;
    }
    .group-color {
      position: sticky;
      left: 0;
    }
    & {
      .edit-task-title-btn {
        opacity: 0;
      }
    }
    &:hover {
      .edit-task-title-btn {
        cursor: pointer;
        height: 24px;
        flex: initial;
        margin-left: 8px;
        color: #676879;
        padding-left: 8px;
        padding-right: 8px;
        border-radius: 4px;
        line-height: 0;
        padding-top: 4px;
        margin-top: 5px;
        opacity: 1;
        &:hover {
          background-color: #f5f6f8;
          opacity: 1;
        }
      }
    }
  }
  .task-title-content {
    // width: 160px;
    max-width: 95%;
    min-width: 95%;
    align-items: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;

    .chat-icon-container {
      display: flex;
      align-items: center;
      font-size: em(18px, 13px);
      margin-right: em(10px, 18px);
      cursor: pointer;
    }
  }
  .bubble-talk {
    color: rgb(197 199 208);
    &.update {
      color: $blue;
    }
    &:hover {
      color: $blue;
    }
  }
  // .task-title {

  // }
}
.icon-down-task {
  visibility: hidden;
  cursor: pointer;
}

.drag-icon {
  visibility: hidden;
  width: 0px;
}

.task-title {
  border: none;
  cursor: pointer;
  font-family: roboto-regular, helvetica, sans-serif;
  // position: sticky;
  width: 95%;
  padding-inline-start: 15px;
  max-width: 15ch;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;

  &:focus-visible {
    outline: none;
    text-overflow: clip;

  }
}

.task-preview {
  & > *
  // :not(:first-child)
  {
    border-inline-start: 1px solid #fff;
  }
  &:hover {
    .task-title-cell,
    .react-datepicker-wrapper,
    .text-cmp,
    .task-text:not(:focus-visible),
    .member-picher-section {
      background-color: #e6e9ef;
    }
  }
}

.task-icon {
  &:hover {
    .icon-down-task {
      visibility: visible;
      transform: translateX(10px);
    }
    .drag-icon {
      visibility: visible;
      width: 0px;
    }
  }
}

.task-icon {
  gap: 10px;
  align-items: center;
}

.task-modal {
  font-size: 14px;
  padding: 5px 20px;
  background-color: #fff;
  box-shadow: 0 8px 16px 0 rgb(0 0 0 / 32%);
  border-radius: 4px;
  cursor: pointer;
  width: 155px;
  padding: 15px;
  position: absolute;
  z-index: 10;
  margin-top: 4.5em;
  margin-left: 1.3em;
}

.finish-task {
  width: 38px;
  border-left: 1px solid white;
  height: 34px;
  background-color: #f5f6f8;
  border-right: 8px solid #e6e9ef;
}

.task-preview-section {
  display: flex;
  width: fit-content;
}

