.board-controllers {
  flex-wrap: wrap;
  margin-left: 10px;

  .add-group-btn {
    font-family: roboto-regular, helvetica, sans-serif;
    height: 32px;
    font-size: 0.875rem;
    padding: 4px 8px;
    font-weight: 400;
    line-height: 22px;
    border: none;
    border-radius: 4px;
    background-color: #0073ea;
    color: #fff;
    width: 90px;
    margin-top: 4px;
    margin-right: 6px;
    transform: translateY(-4px);
  }

  .controller-opt {
    font-family: roboto-regular, helvetica, sans-serif;
    height: 32px;
    font-size: 0.875rem;
    padding: 4px 0px;
    font-weight: 400;
    line-height: 22px;
    border: none;
    border-radius: 4px;
    height: 32px;
    border-radius: 4px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 80px;
    /* margin: 2px; */
    transition: 0.1s;

    &:hover {
      background-color: $grey;
    }
  }
}

.filter-modal {
  display: flex;
  font-size: 0.875rem;
  padding: 8px 24px 24px;
  background-color: #fff;
  box-shadow: -2px 6px 13px 0 rgb(95 87 87 / 32%);
  border-radius: 4px;
  position: absolute;
  /* z-index: 10; */
  margin-top: 2.5em;

  height: 356px;
  z-index: 200;
  margin-left: 7.3em;

  .filter-headline {
    display: flex;
    justify-content: space-between;

    .clear-all-btn {
      margin-right: 10px;
      font-size: 14px;
      color: #98999c;
    }
  }
  .labels-filter {
    flex-direction: column;
    overflow-y: scroll;
  }
  & > * {
    padding-top: 8px;
    color: #323338;
    font-size: rem(16px);
    font-family: 'roboto-regular';
  }

  span {
    padding: 5px;
    color: #676879;
    font-size: rem(14px);
  }

  .status-circle {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    display: inline-block;
    margin-right: 5px;
  }

  .owner-name-circle {
    font-family: 'roboto';
    border-radius: 50%;
    border: none;
    height: 25px;
    width: 25px;
    margin: 3px;
    color: $white;
    text-align: center;
    padding-top: 4px;
    margin-right: 15px;
  }

  li {
    margin-bottom: 6px;
    background-color: #f5f6f8;
    width: 142px;
    border: none;
    align-items: center;
    padding: 0 12px;
    margin-left: 2px;
    height: 32px;
    cursor: pointer;
    border-radius: 4px;
    font-family: roboto;
    color: #323338;
    font-size: rem(13px);
    font-weight: bold;

    &:hover {
      background-color: #e6e9ef;
    }

    &.filterClicked {
      background-color: #cce5ffc2;
    }
  }
}

.column-filter {
  display: flex;
  flex-direction: column;
  margin-inline-end: 6px;
}

.filter-list {
  margin: 0;
  list-style: none;
  padding: 0;
}

.search-container {
  border: 1px solid #0085ff;
  border-color: #0085ff;
  border-radius: 4px;
  width: 214px;
  height: 32px;
  align-items: center;
  background-color: white;

  .input-search {
    border: none;
    width: 90%;
    border-radius: 4px;
    outline: none;
    padding-right: 48px;
    padding-left: 12px;
    transition: width 0.1s ease, height 0.1s ease;
    background-color: white;
    transition: background-color 0.1s cubic-bezier(0, 0, 0.35, 1),
      width 0.1s cubic-bezier(0, 0, 0.35, 1);
  }

  &:hover {
    background-color: white;
  }
}

.board-controllers {
  .controller-opt {
    &:hover {
      background-color: none;
    }
  }
}

.search-button {
  align-items: center;
}

// .input-search-wrapper {
//   margin-left: 10px;
// }

@media (min-width: 640px) {
  .board-controllers {
    width: fit-content;
  }
  .filter-modal {
    width: 470px;
    padding: 8px 17px 10px;
    background-color: #fff;
    margin-left: 0;
    .labels-filter {
      overflow-x: scroll;
      overflow-y: hidden;
      flex-direction: row;
    }
  }
}

@media (min-width: 992px) {
  .filter-modal {
    margin-left: 13.3em;
  }
  .board-controllers {
    flex-wrap: nowrap;
    margin-left: 0px;
    column-gap: 0px;
  }
}
