section.board-header {

  // width: 100%;
  // height: 70px;
  // position: sticky;
  // left:320px;
  // border-bottom: 1px solid rgba(0, 0, 0, 0.233);
  // z-index: 5;
  // position: fixed;
  // margin-bottom: 130px;
  width: 100%;
  margin: 0;
  height: fit-content;
  position: sticky;
  left: 0;
  z-index: 200;

  .icon-btn-container {
    gap: 2px;
    height: 32px;
    padding: 4px 8px;
    border: none;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: fit-content;
    transition: 0.1s;

    button {
      font-size: em(14px);
      color: #323338;
      font-family: roboto-regular, Arial, Helvetica, sans-serif;
    }

    button.dots {
      display: flex;
      align-items: center;
      font-size: 25px;
      padding: 0;
    }

    &:hover {
      background-color: $grey;
    }
  }

  .board-main-title {

    width: 100%;
    height: fit-content;
    margin-bottom: 10px;

    .board-title-container {
      display: flex;
      gap: 6px; ///MAYBE CHANGE IN DESKTOP

      .board-header-title {
        font-size: 1.5rem;
        margin: 0;
        padding: 3px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        color: #323338;
        font-weight: 600;
        font-family: roboto-regular;

        // width: fit-content;
        // text-align: start;
        // height: fit-content;
        // line-height: 3;
        // margin-block-start: 0;
        // margin-block-end: 0; 
        // height: 2.5rem;
        &:hover {
          border: 0.5px solid #c5c7d0;
          border-radius: 4px;
          white-space: normal;
        }

        &:focus-visible {
          outline: none;
        }
      }
    }
  }

  .desc-container {
    // height: ;
    padding: 5px 5px 5px 5px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 95%;
    p {
      color: rgb(103, 104, 121);
      line-height: 1.5;
      font-family: roboto-regular, Arial, Helvetica, sans-serif;
      padding: 0;
      margin: 0;
      font-size: em(14px);

      
    }

    &:hover {
      border: 0.5px dashed #c5c7d0;
      
    }
    &:hover {
      white-space: normal;
    }
  }

  div.board-header-btns {
    margin-right: 2%;
    column-gap: 10px;
    flex-wrap: wrap;
    row-gap: 5px;
    align-items: center;

    .dots-icon {
      grid-area: more;

    }

    .invite {
      grid-area: invite;
    }

    .activity {
      grid-area: activity;
    }

    .last-seen-members {
      grid-area: last-seen;
      justify-content: center;
      cursor: pointer;
      align-items: center;
      height: 100%;

      p {
        font-size: em(14px);
        color: #323338;
        font-family: roboto-regular, Arial, Helvetica, sans-serif;
        margin: 0px 11px 0px 0px;
      }

      .LS,
      .ME,
      .ZR {
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: 'roboto-regular';
        border-radius: 50%;
        border: none;
        height: 25px;
        width: 25px;
        margin-left: -5px;
        display: inline;
        text-align: center;
        padding: 4px;
        vertical-align: middle;
        font-size: 14px;
        color: white;
      }
    }

    div.icon-btn-container.plus-board {
      border: 0.5px solid #c5c7d0;
      min-width: 145px;
      max-width: 145px;

      button {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
    }
  }

  .board-description {
    text-align: start;
    width: fit-content;
    text-align: start;
    margin: 5px;
    padding: 0;
    display: inline;
    padding: 5px 10px;
    font-size: 100%;



    // &:hover {
    //   border: 0.5px dashed #000;
    // }
    &:focus-visible {
      outline: none;
    }
  }

  .board-header-top {
    color: #323338;
    margin: 12px 0px;
    // border-bottom: 1px solid #e6e9ef;
  }
}



