@media (min-width: 992px) {
  section.board-header {
    .board-main-title {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .board-title-container {
        display: flex;
        gap: 4px;
        align-items: center;
      }
    }

    div.board-header-btns {
      column-gap: 10px;
      row-gap: 0px;
      flex-wrap: wrap;


      div.icon-btn-container.plus-board {
        min-width: 130px;
        max-width: 130px;
      }


    }

   
  }
}

@media (min-width: 640px) {
  section.board-header {

    .desc-container {

      max-width: 75ch;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: pre-wrap;
    }

  }
}